.social {
  width: 100%;
  &__list {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    @include listReset();
  }
  &__item {
    &:not(:last-child) {
      margin-right: 1rem;
    }
  }
  &__link {
    display: block;
    text-decoration: none;

    &:focus,
    &:hover {
      & .social__icon {
        fill: darker($btn-bg-color, 2.5%);
      }
    }
  }
  &__icon {
    fill: $color-warning;
  }
}
