.page-form {
  padding: 2.5rem;

  background-color: $bg-form-color;

  @include for-size(phone-only) {
    padding: 0.9375rem;
  }
  &__inner {
    border: none;
  }
  &__title {
    margin-bottom: var(--mb);
    color: $text-form-color;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    @include listReset();
  }
  &__item {
    width: 100%;
    margin-bottom: var(--mb);

    @include for-size(phone-only) {
      display: flex;
      flex-direction: column;
      align-items: center;
    }

    @include for-size(tablet-portrait-up) {
      width: 48%;
      &--full {
        width: 100%;
      }
    }
  }
  &__field {
    display: block;
    padding: var(--padding);

    width: 100%;
    border: none;
    border-radius: var(--border-radius);
  }
  &__comment {
    display: block;
    padding: var(--padding);
    margin-bottom: var(--mb);
    width: 100%;
    height: 6rem;
    font-family: $GoogleSans;
    resize: none;
    border: none;
    border-radius: var(--border-radius);
  }
  & .button {
    display: block;
    margin-left: auto;

    @include for-size(phone-only) {
      margin: auto;
    }
  }

  & .page-form__select {
    display: inline-block;
    width: 100%;
    // height: 34px;
    // line-height: 30px;
    position: relative;
    min-height: 2.25rem;
    font-family: $GoogleSans-Bold;
    font-weight: 600;

    &::before {
      content: ">";
      display: inline-block;

      position: absolute;
      right: -3px;
      top: 3px;
      z-index: 2;
      width: 36px;
      height: 30px;
      text-align: center;
      line-height: 26px;
      color: $color-warning;
      background: white;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      transform: rotate(90deg);
      cursor: pointer;
    }
    & input {
      display: none;
    }
    & .page-form__select-text {
      position: absolute;
      display: block;
      top: 0;
      left: 0;

      padding: 5px 5px 5px 20px;
      width: 100%;
      height: 36px;
      font-family: $GoogleSans-Bold;
      font-weight: 600;
    }
  }
}

.page-form__select input[name="list"]:not(:checked) ~ .page-form__select-text {
  font-family: $GoogleSans-Bold;
  font-size: 14px;
  font-weight: 600;
  background: #ffffff;
}
.page-form__select input[name="list"]:checked ~ .page-form__select-text {
  z-index: 2;
  font-family: $GoogleSans-Bold;
  font-weight: 600;
  color: transparent;
  background: transparent;
}
/* Стилизация выключенного селекта */

.page-form__select-close:disabled
  ~ .page-form__select-content
  .page-form__select-text {
  background: #eeeeee;
  transition: all 0.5s;
}

/* Стилизация блока с опциями. min-height сделана для фикса высоты при абсолютном позиционировании, overflow же сделан для фиксированной высоты(см. ниже) */

.page-form__select-content {
  position: absolute;
  display: block;
  padding-bottom: 0;
  min-height: 36px;

  font-family: GoogleSans-Bold;
  border: 1px solid #ddd;

  overflow: hidden;
  cursor: pointer;

  transition: all 0.5s;
}

/* Если наш селект закрыт, то он имеет высоту 36px(сделано для того, чтобы слишком большие надписи не растягивали его в высоту)  */

.page-form__select-close:not(:checked) ~ .page-form__select-content {
  height: 36px;
  transition: all 0s;
}

/* Все лейбелы(названия опций) изначально скрыты */

.page-form__select-field {
  display: none;

  margin-left: -20px;
  padding: 5px 5px 5px 20px;
  width: 100%;
  min-width: 500px;
  height: 36px;

  font-family: $GoogleSans-Bold;
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  background: white;
  border-top: 1px solid #ddd;
}

/* Тут много объяснять не надо - просто выделение при наведении */

.page-form__select-field:hover {
  background: #eee;
  cursor: pointer;
}

/* Опять же фикс из-за абсолютного позиционирования */

.page-form__select-close:checked ~ .page-form__select-content {
  padding-top: 36px;
  width: 100%;
}

/* Если наш селект открыт, то надо сделать все опции видимыми */

.page-form__select-close:checked
  ~ .page-form__select-content
  .page-form__select-field {
  display: block;
}

/* Если какая-либо опция была выбрана, то сделать ее видимой(при выборе селект автоматически закроется) */

.page-form__select-content input:checked + .page-form__select-field {
  display: block;
  margin-top: -10px;
  border: none;
  background: white;
}

/* При открытии селекта создать подложку во весь экран, при клике на которую селект закроется, а значение останется пустым. background сделан для наглядности */

.page-form__select-close:checked ~ .page-form__select-bg {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 0;
  background: transparent;
}

.page-form__inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;

  & + label {
    position: relative;
    display: inline-block;
    padding-right: 2rem;
    font-size: 100%;
    color: #ffffff;
    background-color: transparent;

    cursor: pointer;
    &::before {
      content: "...";
      position: absolute;
      top: 50%;
      right: 0;
      padding: 0 3px;
      width: 20px;
      height: 20px;
      color: #fff;
      border: 1px solid #fff;
      line-height: 10px;
      border-radius: 3px;
      transform: translateY(-50%);
    }
  }
  & + label * {
    pointer-events: none;
  }
  &:focus + label,
  & + label:hover {
    cursor: pointer;
  }
  &:focus + label {
    outline: 1px dotted #000;
    outline: -webkit-focus-ring-color auto 5px;
  }
  &:focus + label,
  &.has-focus + label {
    outline: 1px dotted #000;
    outline: -webkit-focus-ring-color auto 5px;
  }
}
