.page-contacts {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 2.5rem 0;
  overflow: hidden;
  &--bg {
    background-color: #eeeeee;
  }
  &--bg-map {

    @include for-size(desktop-up) {
      background-image: url("../img/russia_map.svg");
      background-repeat: no-repeat;
      background-position: right top;
      background-size: 65%;
    }
    @include for-size(big-desktop-up) {
      padding-bottom: 18rem;
    }
  }
  &--bg-pic {
    background-image: url("../img/bg/bg-1.png");
    background-repeat: no-repeat;
    background-position: right bottom;
    background-size: 70%;
  }
  &--bg-white {
    background-color: #ffffff;
  }
  & canvas {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    // background-color: #dddddd;
    z-index: 2;
  }
  &__inner {
    position: relative;
    z-index: 3;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
  }
  &__item {
    width: 100%;
    @include for-size(desktop-up) {
      width: 50%;
    }
  }
  &__item-inner {
    @include for-size(desktop-up) {
      padding-right: 1.875rem;
    }
  }
  &__list {
    @include listReset();
    padding-top: calc(var(--padding-and-margin) * 2);

    &--fl {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }
  &__list-item {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin-bottom: var(--padding-and-margin);
  }
  &__list-col-50 {
    width: 100%;

    display: flex;
    flex-wrap: nowrap;
    align-items: center;

    margin-bottom: var(--padding-and-margin);

    @include for-size(tablet-portrait-up) {
      width: 50%;
    }
  }
  &__icon {
    width: 23px;
    height: 23px;
    margin-right: calc(var(--padding-and-margin) / 2);

    @include for-size(tablet-portrait-up) {
      margin-right: calc(var(--padding-and-margin) / 2);
      width: 47px;
      height: 47px;
    }

    &--small {
      width: 1.875rem;
      height: 1.875rem;
    }
  }
  &__text {
    width: 85%;
    font-size: 90%;
    line-height: normal;
    @include for-size(tablet-portrait-up) {
      width: 90%;
      font-size: 100%;
    }
    & b {
      display: block;
      font-family: $GoogleSans-Bold;
    }
  }
  &__link {
    display: block;
    color: $text-color;
    font-weight: 600;
    text-decoration: none;
    &--mail {
      color: $color-primary;
      font-weight: 600;
      font-size: 120%;
    }
    &--phone {
      font-weight: 600;
      font-size: 130%;
    }
  }
  &__box {
    margin-bottom: 2.5rem;
  }
  &__pic {
    width: 30%;
    padding-right: 10px;
  }
  &__info {
    width: 70%;
    font-size: 80%;
    line-height: normal;
  }
  &__info-name {
    margin-bottom: .3rem;
    font-family: $GoogleSans-Bold;
  }
  &__info-contacts {
    & a {
      display: block;
      margin-bottom: .3rem;
    }
  }
}
