.contacts {
  &__list {
    @include listReset();
  }
  &__item {
    margin-bottom: 1rem;
  }
  &__link {
    color: $text-color;
    text-decoration: none;

    &:focus,
    &:hover {
      color: $color-primary;
    }
  }
  &__icon {
    width: 1.25rem;
    padding-right: 0.625rem;
    box-sizing: content-box;
  }
}
