.page-section {
  padding: var(--padding-and-margin) 0;

  @include for-size(tablet-landscape-up) {
    padding: calc(var(--padding-and-margin) * 2) 0;
  }

  &--bg-gray {
    background-color: $bg-color;
  }
  &--bg-white {
    background-color: #ffffff;
  }
  &--bg-20 {
    & .page-section__item {
      @include for-size(tablet-landscape-up) {
        --count-column: 2;
      }
      @include for-size(desktop-up) {
        --count-column: 2;
      }
    }
  }

  & .container {
    position: relative;
  }
  &__link {
    position: absolute;
    top: 0;
    right: 2.5rem;
    display: flex;
    align-items: center;

    color: $color-primary;
    line-height: normal;
    text-decoration: none;

    @include for-size(phone-only) {
      right: 1rem;
      & .page-section__icon {
        display: none;
      }
    }

    &:focus,
    &:hover {
      & .page-section__icon {
        transform: translateX(10%);
        transition: all 0.5s;
      }
    }
  }
  &__icon {
    margin-left: 0.625rem;
    transform: translateX(-10%);
    transition: all 0.5s;
  }
  &__list {
    display: flex;
    flex-wrap: wrap;
    @include listReset();
  }
  &__item {
    flex-grow: 1;

    margin-bottom: var(--mb);
    max-width: calc(100% / var(--count-column) - var(--padding-and-margin));

    @include for-size(phone-only) {
      --count-column: 1;
      --padding-and-margin: 0;
    }
    @include for-size(tablet-portrait-up) {
      margin-right: var(--padding-and-margin);
      --count-column: 2;
    }

    @include for-size(tablet-landscape-up) {
      --count-column: 3;
    }
    @include for-size(desktop-up) {
      --count-column: 4;
    }
  }
  &__item-body {
    color: #ffffff;

    & .title {
      color: #ffffff;
    }
  }
}
