.web {
  & .swiper-slide {
    margin-bottom: 1.25rem;
  }
  @include for-size(tablet-landscape-up) {
    & .swiper-wrapper {
      flex-wrap: wrap;
      justify-content: space-between;
    }
    & .swiper-slide {
      width: 30% !important;
      margin-bottom: 1.25rem;
    }
  }
  @include for-size(desktop-up) {
    & .swiper-slide {
      width: 23% !important;
      margin-bottom: 1.25rem;
    }
  }
  &__form {
    display: none;

    @include for-size(phone-only) {
      display: block;
    }
  }
}
