.swiper-container {
  width: 100%;
}
.swiper-slide {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.parallax-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center;
}
