.page {
  position: relative;
  background-color: $bg-color;
  @include for-size(tablet-landscape-up) {
    display: flex;
    justify-content: space-between;
  }
  & .wrapper {
    width: 100%;
    margin-left: auto;
    @include for-size(tablet-portrait-up) {
      width: calc(100%);
    }
    @include for-size(tablet-landscape-up) {
      width: calc(100% - 31vw);
    }
    @media (min-width: 950px) {
      width: calc(100% - 28vw);
    }
    @media (min-width: 1000px) {
      width: calc(100% - 27vw);
    }
    @media (min-width: 1050px) {
      width: calc(100% - 26vw);
    }
    @include for-size(desktop-up) {
      width: calc(100% - 22vw);
    }
    @media (min-width: 1300px) {
      width: calc(100% - 20vw);
    }
    @include for-size(big-desktop-up) {
      width: calc(100% - 20vw);
    }
  }
  &__title {
    margin-bottom: 1.5rem;
    @include ff(
      "font-size",
      (
        375px: 25px,
        1920px: 50px
      )
    );

    &--white {
      color: #ffffff;
    }
  }
  &__content {
    padding-top: 2.5rem;
  }

  &--canvas {
    position: relative;
    overflow-x: hidden;

    & canvas {
      opacity: 0;
      position: absolute;
      width: 100%;
      height: auto;
      margin: 0;
      padding: 0;
      z-index: -1;

      @include for-size(tablet-landscape-up) {
        opacity: 1;
        height: 100%;
      }
    }
  }
  & hr {
    color: #eeeeee;
    background-color: #eeeeee;
    border: none;
    height: 2px;
  }
  &__text {
    & p {
      margin-bottom: 1.5rem;
    }
    & h1,
    & h2,
    & h3,
    & h4,
    & h5,
    & h6 {
      font-size: 160%;
      line-height: normal;
      margin-bottom: 1.5rem;
    }
  }
}
