.block-result {
  position: relative;
  padding: calc(var(--padding-and-margin) * 4) 0;
  background: $color-primary;
  background-image: url("../img/about/result-bg.png"); /* fallback */
  background-image: url("../img/about/result-bg.png"),
    linear-gradient(to right, $color-primary, lighter($color-primary, 2%)); /* W3C */

  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 70%;
  &__item {
    flex-grow: 1;

    margin-bottom: 1.25rem;
    width: 100%;

    &:first-child {
      order: 1;
    }
    @include for-size(tablet-landscape-up) {
      width: 50%;

      &:first-child {
        order: 0;
      }
    }

    & img {
      padding: 1.25rem;
    }
  }
  &__text {
    color: #ffffff;
    & p {
      margin-bottom: 1.5rem;
    }
    & h1,
    & h2,
    & h3,
    & h4,
    & h5,
    & h6 {
      font-size: 160%;
      line-height: normal;
      margin-bottom: 1.5rem;
    }
  }
  &__footer {
    margin-bottom: 1.5rem;
    color: #ffffff;
  }
  &__footer-ps {
    position: relative;
    margin-bottom: 1.5rem;
    font-family: $GoogleSans-Bold;
    font-size: 120%;

    @include for-size(tablet-landscape-up) {
      font-size: 200%;
      &::before {
        content: "";
        position: absolute;
        top: -10px;
        left: -50px;
        width: 40px;
        height: 40px;
        background-image: url("../img/icons/icon-curved-arrow.svg");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center;
        transform: skewY(10deg) scale(1, -1) rotate(-70deg);
      }
    }
  }
  & .link {
    margin-top: 1.5rem;
    @include for-size(phone-only) {
      position: absolute;
      bottom: 1.25rem;
      left: 50%;
      transform: translateX(-50%);
      display: block;
      width: 70%;
      text-align: center;
    }
  }
  & .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
}
