.history {
  &__list {
    @include listReset();
    padding: calc(var(--padding-and-margin) * 2) 0;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;

    &:not(:last-child) {
      margin-bottom: calc(var(--padding-and-margin) * 2);

      & .history__icon {
        &::after {
          content: "";
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translate(-50%, 0%);
          width: 1px;
          height: calc(
            var(--padding-and-margin) * calc(var(--line-height-num) * 3.5)
          );
          border-left: 2px dashed darker($bg-color, 6%);
        }
      }
    }

    &:first-child {
      @include for-size(big-desktop-up) {
        padding-bottom: calc(var(--padding-and-margin) * 4);
      }
    }

    &:nth-child(2) {
      & .history__icon {
        &::after {
          content: "";
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translate(-50%, 0%);
          width: 1px;
          height: calc(var(--padding-and-margin) * var(--line-height-num));
          border-left: 2px dashed darker($bg-color, 6%);

          @include for-size(tablet-portrait-up) {
            height: calc(var(--padding-and-margin) * 5);
          }
        }
      }
    }

    @include for-size(big-desktop-up) {
      padding-left: calc(var(--padding-and-margin) * 2);
      padding-right: calc(var(--padding-and-margin) * 2);
    }
  }
  &__icon {
    // flex-grow: 1;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 3rem;
    height: 3rem;
    font-size: 120%;
    font-weight: 600;
    color: #ffffff;
    background-color: $color-warning;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50%;

    @include for-size(tablet-portrait-up) {
      width: 5rem;
      height: 5rem;
    }
    @include for-size(tablet-landscape-up) {
      width: 6rem;
      height: 6rem;
    }

    &--primary {
      background-color: $color-primary;
    }
  }
  &__info {
    // flex-grow: 1;
    width: 80%;
    font-size: 80%;
    line-height: normal;
    color: $text-color;

    @include for-size(tablet-portrait-up) {
      width: 75%;
      font-size: 100%;
    }
  }
  &__info-warning {
    margin-top: 2.5rem;
  }
  & .swiper-button-prev {
    position: absolute;

    background-image: none;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%) rotate(180deg);
      padding: 1.25rem;
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 50%;
      background-color: #ffffff;
      border-radius: 50%;
    }

    @include for-size(phone-only) {
      opacity: 0;
    }
  }
  & .swiper-button-next {
    position: absolute;

    background-image: none;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      padding: 1.25rem;
      width: 20px;
      height: 10px;
      background-image: url("../img/icons/icon-menu_arrow.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 50%;
      background-color: #ffffff;
      border-radius: 50%;
    }
    &::after {
      content: "Назад в прошлое";
      position: absolute;
      right: 0;
      bottom: -40px;
      font-size: 80%;
      line-height: normal;
      text-align: right;
      color: $color-primary;
    }

    @include for-size(phone-only) {
      opacity: 0;
    }
  }
  &.swiper-container {
    @include for-size(desktop-up) {
      padding: 0 5rem;
    }
  }
}
