.side-elements {
  height: 100%;

  background-color: #ffffff;
  z-index: 1000;

  @include for-size(tablet-landscape-down) {
    display: none;
    width: 100%;
  }

  @include for-size(tablet-landscape-up) {
    position: fixed;
    top: 0;
    left: 0;
  }
  @include for-size(desktop-up) {
    width: auto;
    min-width: 20vw;
  }
}
